<template>
  <div class="tickets-maintain">
    <div class="grid-container">
      <div class="left">
        <div class="title">财政票据库存所有的票据种类</div>
        <div class="search">
          <el-input v-model.trim="searcher" clearable placeholder="输入票据编码或票据名称进行搜索"></el-input>
          <div style="width: 20px"></div>
          <el-button class="searchBtn" type="primary" icon="el-icon-search" @click="queryFinanceBills">查询</el-button>
        </div>

        <el-tree class="tree"
                 :data="financeBills"
                 :props="financeBills"
                 ref="financeBillTree"
                 @check="changeAddBtnStatus"
                 default-expand-all
                 :show-checkbox="showCheckbox">
        </el-tree>
      </div>
      <div class="middle">
        <div class="addBtn">
          <el-button @click="add" type="primary" size="large" :disabled="isAddBtnDisabled"> &gt; </el-button>
        </div>
        <div class="removeBtn">
          <el-button @click="remove" type="primary" size="large" :disabled="isRemoveBtnDisabled"> &lt; </el-button>
        </div>
      </div>
      <div class="right">
        <div class="title">列入监控的票据种类</div>
        <el-tree class="tree"
                 :data="chosenBills"
                 :props="chosenBills"
                 ref="chosenBillTree"
                 @check="changeRemoveBtnStatus"
                 show-checkbox>
        </el-tree>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'tickets-maintain',
  data () {
    return {
      searcher: null,
      showCheckbox: true,
      billNumLimit: 10,
      financeBills: [],
      chosenBills: [],
      isAddBtnDisabled: true,
      isRemoveBtnDisabled: true
    }
  },
  created () {
    this.initTransfer()
  },
  methods: {
    /**
     初始化 穿梭框
     */
    initTransfer () {
      this.queryChosenBills().then(this.queryFinanceBills())
    },
    /**
     将后台的json转成jsonTree格式
     */
    jsonTree (data, config) {
      // 1.声明了变量，拿到传参过来的值，注意这里拿到的是string类型，不是对应的值
      let id = config?.id || 'id'
      let pid = config?.pid || 'pid'
      let children = config?.children || 'children'
      let idMap = []
      // let newIdMap = []
      let jsonTree = []
      // 2.v[id]==v[“id”]==v.id
      // idMap数组的下标对应着id为下标的相应json数据
      // a[1]对应着id为1的json数据，以此类推
      data.forEach(function (v) {
        idMap[v[id]] = v
      })
      // 3拿到当前遍历的父元素id
      // 根据父元素id，判断数组里是否存在这样一条数据
      // 存在，就判断父元素是否有子元素数组，若没有就添加一个children数组（传参过来的）
      // 把当前元素添加父元素的children数组里
      // 不存在，就直接添加到jsonTree里
      data.forEach(function (v) {
        v.label = v.billCode + ' ' + v.label
        let parent = idMap[v[pid]]
        // 定义一个newIdMap
        delete v.parent// 删除{v}的parent和id
        // delete v.id
        if (parent) {
          !parent[children] && (parent[children] = [])
          parent[children].push(v)
        } else {
          jsonTree.push(v)
        }
      })
      return jsonTree
    },
    /**
     查询 财政票据库存所有的票据种类
     */
    async queryFinanceBills () {
      let { data } = await this.$api.queryData({
        params: {
          mapperId: 'com.bosssoft.monitor.dao.MonitorBillTypeMapper.queryFinanceBills',
          searcher: this.searcher
        }
      })
      if (data.data.length > 0) {
        this.showCheckbox = true
        let billTree = this.jsonTree(data.data)
        this.financeBills = [{
          id: 0,
          label: '票据种类',
          children: billTree
        }]
        this.chosenBills.forEach((item) => {
          this.disableLeaf(this.financeBills[0], item)
        })
      } else {
        this.showCheckbox = false
        this.financeBills = [{
          id: 0,
          label: '未找到符合条件的票据'
        }]
      }
    },
    /**
     查询 列入监控的票据种类
     */
    async queryChosenBills () {
      let { data } = await this.$api.queryData({
        params: {
          mapperId: 'com.bosssoft.monitor.dao.MonitorBillTypeMapper.queryChosenBills'
        }
      })
      this.chosenBills = []
      let bills = data.data
      if (bills && bills?.length > 0) {
        bills.forEach((item, index) => {
          this.chosenBills.push({
            id: item.billId,
            label: null
          })
        })
      }
    },
    /**
     禁用节点
     */
    disableLeaf (financeBillnode, addBillNode) {
      if (financeBillnode.children) {
        for (let i = 0; i < financeBillnode.children.length; i++) {
          this.disableLeaf(financeBillnode.children[i], addBillNode)
        }
        // 判断子节点是否都已禁用
        let cnt = 0
        financeBillnode.children.forEach((item) => {
          if (item.disabled) {
            cnt++
          }
        })
        // 父节点禁用
        if (cnt === financeBillnode.children.length) {
          financeBillnode.disabled = true
        }
      } else {
        if (financeBillnode.id === addBillNode.id) {
          financeBillnode.disabled = true
          // 获取对应的票据名称
          addBillNode.label = financeBillnode.label
        }
      }
    },
    /**
     启用节点
     */
    enableLeaf (financeBillnode, removeBillNode) {
      if (financeBillnode.children) {
        for (let i = 0; i < financeBillnode.children.length; i++) {
          this.enableLeaf(financeBillnode.children[i], removeBillNode)
        }
        // 判断子节点是否都已启用
        let cnt = 0
        financeBillnode.children.forEach((item) => {
          if (!item.disabled) {
            cnt++
          }
        })
        // 父节点启用
        if (cnt === financeBillnode.children.length) {
          financeBillnode.disabled = false
        }
      } else {
        if (financeBillnode.id === removeBillNode.id) {
          financeBillnode.disabled = false
        }
      }
    },
    /**
     添加 监控票据
     */
    async add (fromData, toData, obj) {
      let addBillsTmp = this.$refs.financeBillTree.getCheckedNodes()
      // 过滤非叶结点
      let addBills = []
      addBillsTmp.forEach((item) => {
        if (item.isLeaf === 1) {
          addBills.push({
            id: item.id,
            label: item.label
          })
        }
      })
      // 判断票据数量是否超过预设值
      if (addBills.length + this.chosenBills.length <= this.billNumLimit) {
        await this.$api.add({
          data: {
            mapperId: 'com.bosssoft.monitor.dao.MonitorBillTypeMapper.batchInsert',
            billIds: JSON.stringify(addBills.map(item => item.id))
          }
        })
        let financeBillsTmp = JSON.parse(JSON.stringify(this.financeBills))
        // 添加到右边穿梭框中，并禁用左边对应的节点
        addBills.forEach((item) => {
          this.chosenBills.push(item)
          this.disableLeaf(financeBillsTmp[0], item)
        })
        this.financeBills = financeBillsTmp
        // 更新按钮状态
        this.isAddBtnDisabled = true
      } else { // 票据数量超过预设值
        if (document.getElementsByClassName('el-message').length === 0) {
          this.$message.info('最多添加' + this.billNumLimit + '种票据！')
        }
      }
    },
    /**
     移除 监控票据
     */
    async remove () {
      let removeBills = this.$refs.chosenBillTree.getCheckedNodes()
      await this.$api.remove({
        params: {
          mapperId: 'com.bosssoft.monitor.dao.MonitorBillTypeMapper.batchDelete',
          billIds: JSON.stringify(removeBills.map(item => item.id))
        }
      })
      let financeBillsTmp = JSON.parse(JSON.stringify(this.financeBills))
      removeBills.forEach((item) => {
        // 从chosenBillTree 中剔除
        let id = item.id
        this.chosenBills.splice(this.chosenBills.findIndex(item => item.id === id), 1)
        // 启用左边对应的节点
        this.enableLeaf(financeBillsTmp[0], item)
      })
      this.financeBills = financeBillsTmp
      // 更新按钮状态
      this.isAddBtnDisabled = true
      this.isRemoveBtnDisabled = true
    },
    /**
     根据是否有选择节点 更新添加按钮状态
     */
    changeAddBtnStatus (currentObj, treeStatus) {
      this.isAddBtnDisabled = !treeStatus.checkedKeys.length > 0
    },
    /**
     根据是否有选择节点 更新移除按钮状态
     */
    changeRemoveBtnStatus (currentObj, treeStatus) {
      this.isRemoveBtnDisabled = !treeStatus.checkedKeys.length > 0
    }
  }
}
</script>

<style lang="scss" scoped>
  .el-button {
    border-radius: 20px;
  }
  .title {
    height: 20px;
    width: 100%;
    text-align: center;
    border: 1px solid #a2a2a2;
    border-bottom: 0px;
    background-color: #eaeaea;
    padding: 10px 0;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
  }
  .search {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 40px;
    border: 1px solid #a2a2a2;
    border-bottom: 0px;
    border-top: 0px;
    padding: 5px 0;
    .el-input {
      flex: 10;
    }
    .searchBtn {
      flex: 0.5;
      border-radius: 0px;
    }
  }
  .el-tree {
    min-width:100%;
    font-size:14px;
    display: inline-block;
  }
  .tree {
    overflow-y:scroll;
    overflow-x: scroll;
    height: 100%;
    padding: 8px 0;
    /*width:200px;*/
    border: 1px solid #a2a2a2;
    border-top: 0px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
  }
  .grid-container {
    height: 600px;
    width: 98%;
    padding: 0 10px;
    display: flex;
    flex-direction: row;
    .left {
      display: flex;
      flex: 5;
      flex-direction: column;
    }
    .middle {
      display: flex;
      flex: 0.7;
      flex-direction: column;
    }
    .right {
      display: flex;
      flex: 5;
      flex-direction: column;
    }
    .addBtn {
      margin: 200px 20px 0 20px;
      padding: 15%
    }
    .removeBtn {
      margin: 10px 20px 0 20px;
      padding: 15%
    }
  }
</style>
